<template>
	<view class="container">
		<view class="list">
			<view class="item" v-if="defaultAddress" @click="selectAddr(defaultAddress.id)">
				<view class="content">
					<view class="top">
						<view class="text1 line">{{defaultAddress.province+defaultAddress.city+defaultAddress.county+defaultAddress.address}}</view>
						<view class="text2">默认</view>
					</view>
					<view class="bot">{{defaultAddress.name+' '+defaultAddress.mobile}}</view>
				</view>
			</view>
			<view class="item" v-for="(item,index) in basicAddress" v-if="basicAddress.length>0" @click="selectAddr(item.id)">
				<view class="content">
					<view class="top">
						<view class="text1 line">{{item.province+item.city+item.county+item.address}}</view>
					</view>
					<view class="bot">{{item.name+' '+item.mobile}}</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {mapGetters} from 'vuex'
	export default{
		data(){
			return {
				
			}
		},
		onLoad(){
			
		},
		computed:{
			...mapGetters(['defaultAddress','basicAddress'])
		},
		methods:{
			// 选择收货地址
			selectAddr(id){
				let pages = getCurrentPages();  //获取所有页面栈实例列表
				let nowPage = pages[ pages.length - 1];  //当前页页面实例
				let prevPage = pages[ pages.length - 2 ];  //上一页页面实例
				prevPage.$vm.addrId = id;   //修改上一页data里面的addrId参数值为id
				uni.navigateBack()
			}
		}
	}
</script>

<style lang="scss">
	.container{
		.list{
			padding-bottom: 135upx;
			.item{
				background-color: #fff;
				padding: 20upx 10upx 20upx 30upx;
				display: flex;
				align-items: center;
				justify-content: space-between;
				border-bottom:1px solid #F0F0F0 ;
				.content{
					.top{
						display: flex;
						align-items: center;
						justify-content: flex-start;
						.text1{
							max-width: 500upx;
							font-size: 28upx;
						}
						.text2{
							font-size: 20upx;
							color: #7CC58E;
							border: 1px solid #7CC58E;
							border-radius: 10upx;
							padding:0 10upx ;
							margin-left: 12upx;
						}
					}
					.bot{
						font-size: 24rpx;
						color: $uni-secondary-color;
						margin-top: 10upx;
					}
				}
				.icon{
					width:100upx;
					height: 100upx;
					padding: 25upx;
				}
			}
		}
	}
</style>